import React from 'react'

const Disclaimer = ({}) => (
  <div className='disclaimer'>
    <h5>Aviso</h5>
    <p>
      A informao acima no  equivalente a um conselho legal, onde um advogado aplica a lei para suas circustncias especficas.
      Sendo assim, insistimos que consulte um advogado se quiser ter conselhos na interpretao dessa informao ou em sua preciso.
      Resumindo, voc no deve confiar nisso como conselho legal, nem como uma recomendao de qualquer entendimento legal particular.
    </p>
  </div>
)

export default Disclaimer



